import request from '@/utils/request'
// 上传文件
export function uploadFile (data) {
  return request({
    url: '/files/upload',
    method: 'post',
    data
  })
}

// 全部文件
export function getAllFiles () {
  return request({
    url: '/files/filelist',
    method: 'get'
  })
}

export function downloadFile (id) {
  return request({
    url: `/files/download?id=${id}`,
    method: 'get'
  })
}
// 删除删除文件  '/files/delete?id=?'
export function deleteFile (id) {
  return request({
    url: `/files/delete?id=${id}`,
    method: 'get'
  })
}
// 文件  '/files/delete?id=?'
export function searchFile (keyword) {
  return request({
    url: `/files/findfile?keyword=${keyword}`,
    method: 'get'
  })
}
